import axios from "axios";
import { createStore } from "vuex";

const store = createStore({
    state() {
        return {
            name: 'kim',
            age: 20,
            likes: 30,
            checkLikes: false,
            more: {},
        }
    },
    mutations: {
        /* 
            ë³€ê²½ í•¨ìˆ˜ ì •ì˜ 
            axios.get().then() í•˜ë©´ ë˜ì§€ ì•Šì„ê¹Œìš”
            1.nameì„ ë°”ê¾¸ê³   mutation í•¨ìˆ˜ 3ì´ˆ
            2.ageë¥¼ ë°”ê¾¸ë ¤ë©´ mutation í•¨ìˆ˜ 4ì´ˆ 
        
        */
        setMore(s, data) {
            s.more = data
        },
        changeName(state) {
            state.name = 'park'
        },
        addAge(state, payload) {
            state.age += payload;
        },
        increaseLike(state) {
            if (state.checkLikes == false) {
                state.likes++;
                state.checkLikes = true;

            } else {
                state.likes--;
                state.checkLikes = false;
            }
        },
    },
    actions: {
        /* 
            ajaxë¥¼ ìš”ì²­í•˜ëŠ” ê³³  

            dispatch('getData')ì„ ê°€ì ¸ì˜¨ë‹¤
            ë‹¤ë¥¸ ì»´í¬ë„ŒíŠ¸ì™€ ê³µìœ í•˜ê³  ì‹¶ìœ¼ë©´ 
            ajaxëŠ” action : dispatch 
            stateë³€ê²½ mutations : commit 
        */
        getData(context) {
            axios.get('https://codingapple1.github.io/vue/more0.json')
                .then((a) => {
                    console.log(a.data);
                    context.commit('setMore', a.data);
                })
        }
    },

})

export default store;